import styled from "styled-components";

export const Work = styled.div`
  position: relative;
  .ant-divider {
    background: #ccc;
  }
  ul {
    padding-left: 12px;
    li {
      padding-left: 6px;
      line-height: 1.2;
    }
  }
  img {
    max-width: 100%;
    &.right {
      float: right;
      max-width: 80%;
    }
  }
  .year {
    text-align: right;
    @media (max-width: 575px) {
      text-align: left;
    }
  }
  .company,
  .position {
    //@media (max-width: 575px) {
    margin: 0;
    //}
  }
  .description {
    //@media (max-width: 575px) {
    margin-top: 16px;
    //}
  }
  @media print {
    //page-break-after: always;
    //&:nth-child(3) {
    //  page-break-after: always;
    //}
    img {
      display: none;
    }
  }
`;

export const TechStack = styled.i`
  display: block;
  font-weight: 700;
`;
