import * as React from "react";
import { TechStack } from "../styled";
import { isAppleEnv } from "../../../utils";

export default {
  title: "OCBC Bank (Singapore)",
  year: "2022/1-Present",
  position: ["Mobile Developer"],
  description: (
    <div>
      <p>OCBC Bank is a second largest bank in Singapore</p>
      <ul>
        <li>Maintain and develop company's app</li>
        <li>
          Migrated legacy code from Backbone.js to React for improving site
          performance
        </li>
        <li>Hybrid mode for using React as core, and wrap it by Cordova</li>
        <li>Work with Native developer to integrate Native feature</li>
        <li>Worked on some bespoke projects as well using React</li>
        <li>
          Guided and assisted the Project Management teams in coordinating fixes
          and deployments to the app for company's app
        </li>
        <li>
          Experience in task tracking using Jira. Worked independently in some
          projects and as part of multiple Agile Scrum development teams
        </li>
        <li>
          Code review with other developer's PR and implemented SonarQube to
          improve code quality
        </li>
      </ul>
      <TechStack>
        Tech Stack: JavaScript, TypeScript, CSS, HTML, ES6, React.js, Redux,
        Redux-Saga, React-Router, Webpack
      </TechStack>
      <a
        href={
          isAppleEnv()
            ? "https://apps.apple.com/sg/app/ocbc-sg-mobile-banking/id292506828"
            : "https://play.google.com/store/apps/details?id=com.ocbc.mobile&hl=en_SG&gl=US"
        }
        target="_blank"
      >
        Download the app here
      </a>
    </div>
  )
};
