import "react-app-polyfill/ie9";
import "react-app-polyfill/stable";
import "./style.css";

import * as React from "react";
import * as ReactDOM from "react-dom";

import { ConfigProvider, Divider, Layout } from "antd";

import enUS from "antd/es/locale/en_US";
import zhCN from "antd/es/locale/zh_CN";
import zhTW from "antd/es/locale/zh_TW";

import Profile from "./components/Profile";

import Education from "./components/Education";
import Skill from "./components/Skill";
import Work from "./components/Work";
import Projects from "./components/Projects"
import Portfolio from "./components/Portfolio";
// import Download from "./components/Download"

import * as serviceWorker from "./serviceWorker";

const languageSelector: {
  [x: string]: { title: string; antdLanguage: any };
} = {
  us: {
    title: "Resume",
    antdLanguage: enUS
  },
  ch: {
    title: "履历表",
    antdLanguage: zhCN
  },
  tw: {
    title: "履歷表",
    antdLanguage: zhTW
  }
};
class App extends React.PureComponent<
  any,
  { language: string; antdLanguage: any }
> {
  constructor(props: any) {
    super(props);
    this.state = {
      language: "us",
      antdLanguage: enUS
    };
  }
  changeLanguage = (language: string) => {
    document.title = languageSelector[language].title;
    this.setState({
      language: language,
      antdLanguage: languageSelector[language].antdLanguage
    });
  };
  render() {
    return (
      <ConfigProvider locale={this.state.antdLanguage}>
        <Layout style={{ position: "relative" }}>
          <Layout.Content style={{ padding: "0 20px" }}>
            <Profile
              language={this.state.language}
              changeLanguage={this.changeLanguage}
            />
            <Divider />
            <Education language={this.state.language} />
            <Divider />
            <Skill language={this.state.language} />
            <Divider />
            <Work language={this.state.language} />
            <Divider />
            <Projects language={this.state.language} />
            <Divider />
            <Portfolio language={this.state.language} />
            {/*<Download language={this.state.language} />*/}
          </Layout.Content>
        </Layout>
      </ConfigProvider>
    );
  }
}
ReactDOM.render(<App />, document.getElementById("root"));
serviceWorker.register();
